





























































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			type:0,
			malllist:[
			],
			typeList:[
				{name:'合页系列'},
				{name:'铰链系列'},
				{name:'插销系列'},
				{name:'合页系列'},
				{name:'合页系列'},
				{name:'合页系列'}
			],
			mallnum:0,
			mallprice:0,
			pop:false,
			storeid:'',
			classid:'',
			class_id:''
		}
	},
	onLoad(opt) {
		var malllist=[
			{img:'../../static/build/mall.png',price:38,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:11.22,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:1000.31,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:38.23,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:38.53,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:138.42,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:48,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:19.22,name:'不锈钢加厚荷叶',num:'300'},
			{img:'../../static/build/mall.png',price:38.99,name:'不锈钢加厚荷叶',num:'300'}
		];
		this.malllist=this.malllistmap(malllist);
		this.classid=opt.classid;
		this.storeid=opt.storeid;
		this.req_storeclasslist()
	},
	methods: {
		cg_pop(){
			if(!this.pop&&!this.mallnum){
				return;
			}
			this.pop=!this.pop;
		},
		cg_type(type){
			this.type=type;
		},
		go_build_pay(){
			wx.navigateTo({
				url: '../build/pay'
			})
		},
		go_build_mallinf(){
			wx.navigateTo({
				url: '../build/mallinf'
			})
		},
		minus(index){
			this.malllist[index].mallnum-=1;
			this.mallnum-=1;
			this.mallprice=(Number(this.mallprice)-Number(this.malllist[index].price)).toFixed(2);
			if(this.mallnum==0){
				this.pop=false;
			}
		},
		add(index){
			this.malllist[index].mallnum+=1;
			this.mallnum+=1;
			this.mallprice=(Number(this.mallprice)+Number(this.malllist[index].price)).toFixed(2);
		},
		malllistmap(arr){
			return arr.map((item)=>{
				item.price=Number(item.price).toFixed(2);
				item.mallnum=0;
				return item;
			})
		},
		cleanbar(){
			this.mallnum=0;
			this.mallprice=0;
			this.malllist=this.malllistmap(this.malllist);
			this.pop=false;
		},
		req_storeclasslist(){
			ut.request({
				data: {
					storeid:this.storeid
				},
				url: "goods/storeclasslist"
			}).then(data=>{
				this.typeList=data;
				this.class_id=data[0].id;
			}).then(data=>{
				this.req_goodslist();
			})
		},
		req_goodslist(){
			ut.request({
				data: {
					storeid:this.storeid,
					classid:this.classid,
					class_id:this.class_id
				},
				url: "goods/goodslist"
			}).then(data=>{
				//this.malllist=data;
			})
		}
	}
}



